img.pic-of-me {
    width: auto;
    height: 300px;
    max-width: 90%;
    background-repeat: no-repeat;
    background-size: contain;
    border-radius: 1em;
    margin: 0 auto;
    margin-top: 4em;
    margin-bottom: 4em;
    -webkit-transform: rotate(90deg);
        -ms-transform: rotate(90deg);
            transform: rotate(90deg);
}

@media (max-width: 450px) {
    img.pic-of-me {
        max-width: 100%;
        height: 200px;
        background-repeat: no-repeat;
        background-size: contain;
        border-radius: 1em;
        margin: 0 auto;
        margin-top: 3em;
        margin-bottom: 3em;
        -webkit-transform: rotate(90deg);
            -ms-transform: rotate(90deg);
                transform: rotate(90deg);
    }
}


